import React, { Component } from 'react';
import { Button, Table } from 'antd';
const server = 'http://127.0.0.1:8000';

class RiskyShifts extends Component {

    constructor(props) {
        super(props);
        this.state = this.props.content
        this.userBind = this.userBind.bind(this);

        this.columns = [
            {
                title: '班次号',
                dataIndex: 'No',
                key: 'No',
            },
            {
                title: '出发地',
                dataIndex: 'start_p',
                key: 'start_p',
            },
            {
                title: '到达地',
                dataIndex: 'end_p',
                key: 'end_p',
            },
            {
                title: '发车日期',
                dataIndex: 'date',
                key: 'date',
            },
            {
                title: '风险等级',
                dataIndex: 'danger_level',
                key: 'danger_level',
            },
            {
                title: '操作',
                key: 'action',
                render: (text, record) => this.action(this.props.role, record.shift_id, record.No)
            }
        ];
    }

    userBind(id, No) {
        fetch(
            server + '/transport/register?uid=4&shift_id=' + id
        )
            .then(res => res.json())
            .then(data => {
                console.log(data)
                alert("成功绑定列车" + No + "!")
            })
            .catch(e => console.log('错误:', e))
    }

    delete(id) {
        fetch(
            server + '/transport/delete?shift_id=' + id
        )
            .then(res => res.json())
            .then(data => {
                console.log(data)
                alert("成功删除纪录：" + id)
            })
            .catch(e => console.log('错误:', e))
    }

    action(r, sft_id, No) {
        if (r === 'user') return (<Button type="primary" onClick={e => { e.preventDefault(); this.userBind(sft_id, No) }}>绑定</Button>)
        else if (r === 'admin') return (<Button type="primary" onClick={e => { e.preventDefault(); this.delete(sft_id) }}>删除</Button>)
        else return null
    }

    render() {
        return (
            <Table className="tble" columns={this.columns} dataSource={this.state}>
                {console.log(this.state)}
            </Table>
        )
    }
}

export default RiskyShifts;