import React,{Component} from 'react';
import { Input, Space, Button,Typography,Radio,DatePicker } from 'antd';
import RiskyShifts from './riskyshifts'
import './trans.css'
const server = 'http://127.0.0.1:8000';
const { Title } = Typography;

class RiskLevel extends Component {
	state = {
	  value: 0,
	};
  
	onChange = e => {
	  console.log('radio checked', e.target.value);
	  this.setState({
		value: e.target.value,
	  },
		() => this.props.onChange(this.state.value));
	};
  
	render() {
	  return (
		<Radio.Group onChange={this.onChange} value={this.state.value}>
		  <Radio value={3}>高风险</Radio>
		  <Radio value={2}>风险</Radio>
		  <Radio value={1}>低风险</Radio>
		  <Radio value={0}>安全</Radio>
		</Radio.Group>
	  );
	}
  }

class PubTrans extends React.Component {
	  constructor(props) {
		super(props);
		this.state = {
		  shift_id: 0,
		  No: "",
		  start_t: "",
		  start_p: "",
		  end_p: "",
		  danger_level: "",
		  allShifts: []
		}
		this.onDateChange = this.onDateChange.bind(this);
		this.onShiftChange = this.onShiftChange.bind(this);
		this.write = this.write.bind(this);
		this.sendWarning = this.sendWarning.bind(this);
	  }
	
	  onShiftChange(e) {
		this.setState({
		  No: e.target.value
		})
	  }
	
	  onStart_pChange(e) {
		this.setState({
		  start_p: e.target.value
		})
	  }
	
	  onEnd_pChange(e) {
		this.setState({
		  end_p: e.target.value
		})
	  }
	
	  onDateChange(date, dateString) {
		this.setState({
		  start_t: dateString
		})
	  }
	
	  write() {
		fetch(
		  server + '/transport/record/?No=' + this.state.No
		  +  '&start_p=' + this.state.start_p + '&end_p=' + this.state.end_p 
		  + '&date=' + this.state.start_t + '&danger_level=' + this.state.danger_level
		)
		  .then(res => res.json())
		  .then(data => {
			console.log(data);
			alert("导入成功!")
		  })
		  .catch(e => console.log('错误:', e))
	  }
	
	  getshift() {
		fetch(
		  server + '/transport/shifts/'     
		)
		  .then(res => res.json())
		  .then(data => {
			console.log(data);
			this.setState({
			  allShifts: data
			})
		  })
		  .catch(e => console.log('错误:', e))
	  }
	
	  sendWarning() {
		fetch(
		  server + '/transport/mail/'
		)
		  .then(res => res.json())
		  .then(data => {
			  console.log(data);
			  alert("成功发送邮件!")
		  })
		  .catch(e => console.log('错误:', e))
	  }
	
	  render() {
		this.getshift()
		return (
		  <div>
			<div className="container">
			<Space direction="vertical">
			  <Input className="input-width" placeholder="班次号" onChange={e => this.onShiftChange(e)} />
			  <Input className="input-width" placeholder="起始地" onChange={e => this.onStart_pChange(e)} />
			  <Input className="input-width" placeholder="目的地" onChange={e => this.onEnd_pChange(e)} />
			  <DatePicker className="input-width" onChange={(date, dateString) => this.onDateChange(date, dateString)} />
			  <RiskLevel onChange={(lv) => this.setState({ danger_level: lv })} />
			  <Button type="primary" onClick={() => this.write()}>导入</Button>
			</Space>
			</div>
	
			<div className="container">
			  <center><Title level={3}>当前班次表</Title></center>
			  <div className="table-container">
				<RiskyShifts content={this.state.allShifts} role='admin'/>
			  </div>
			  <center>
				<Button type="primary" onClick={() => this.sendWarning()}>一键发布提醒</Button>
			  </center>
			</div>
		  </div>
		)
	  }
}

export default PubTrans